import { Injectable } from '@angular/core';
import { TodoItem } from './TodoItem'
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({ providedIn: 'root' })
export class TaskService {

  private tasksUrl = 'api/task';  // URL to web api

  constructor(
    private http: HttpClient) { }

  /** GET heroes from the server */
  getTasks (): Observable<TodoItem[]> {
    return this.http.get<TodoItem[]>(this.tasksUrl)
      .pipe(
        tap(_ => this.log('fetched todoItem')),
        catchError(this.handleError('getTasks', []))
      );
  }


  /** POST: add a new hero to the server */
  addTask (todoitem: TodoItem): Observable<TodoItem> {
    return this.http.post<TodoItem>(this.tasksUrl, todoitem, httpOptions).pipe(
      tap((newTodoItem: TodoItem) => this.log(`added newTodoItem `)),
      catchError(this.handleError<TodoItem>('todoItem'))
    );
  }

  // /** DELETE: delete the hero from the server */
  deleteTask (todoItem: TodoItem | number): Observable<TodoItem> {
    const id = typeof todoItem === 'number' ? todoItem : todoItem.id;
    const url = `${this.tasksUrl}/${id}`;

    return this.http.delete<TodoItem>(url, httpOptions).pipe(
      tap(_ => this.log(`deleted TodoItem id=${id}`)),
      catchError(this.handleError<TodoItem>('deleteTask'))
    );
  }

  /** PUT: update the hero on the server */
  updateTask (todoItem: TodoItem): Observable<any> {
    return this.http.put(this.tasksUrl, todoItem, httpOptions).pipe(
      tap(_ => this.log(`updated todoItem id=${todoItem.id}`)),
      catchError(this.handleError<any>('todoItem'))
    );
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a HeroService message with the MessageService */
  private log(message: string) {
    //this.messageService.add(`HeroService: ${message}`);
  }
}
