import { Component, OnInit } from '@angular/core';
import { TodoItem } from '../TodoItem';
import { TaskService } from '../tasks.serivce';

@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  styleUrls: ['./tasks.component.css']
})

export class TasksComponent implements OnInit {
  tasks: TodoItem[];

    todo : TodoItem = {
        id:1,
        description:'to do',
        createdtime:'1990',
        iscompleted:false 
  }
  constructor(private taskservice: TaskService) { }

  ngOnInit() {
    this.getTasks();
  }

  getTasks(): void {
    this.taskservice.getTasks()
      .subscribe(tasks => this.tasks = tasks);
  }

  add(description: string): void {
    description = description.trim();
    if (!description) { return; }

    let item: TodoItem = {
      id: 1,
      description: description,
      createdtime: '1990',
      iscompleted: false
    }

    this.taskservice.addTask(item)
      .subscribe(todoitem => {
        //this.tasks.push(todoitem);
      });
    this.getTasks();
  }

  delete(todoitem: TodoItem): void {
    this.tasks = this.tasks.filter(h => h !== todoitem);
    this.taskservice.deleteTask(todoitem).subscribe();
  }

}
